<template>
    <div class="col-12" v-if="ready">
        <v-data-table
            :headers="headers"
            :items="userrecord"
            class="elevation-1"
            :rows-per-page-items="10"
            >
            <template v-slot:items="props">
                <td>{{ props.item.date }}</td>
                <td>{{ props.item.leave_id }}</td>
                <td>{{ props.item.assigned_id }}</td>
                <td>{{ props.item.day }}</td>
                <td>{{ props.item.session+1 }}</td>
                <td>{{ props.item.class }}</td>
                <td>{{ props.item.location }}</td>
                <td>{{ props.item.app_type }}</td>
                <td>{{ props.item.type }}</td>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>        
            </template>
        </v-data-table>
    </div>
</template>
<script>
export default {
    data(){
        return{
            userrecord:[],
            ready:false,
            show_res:false,
            headers:[
                { text: 'Date',value :'date',sortable: false},
                { text: 'Leave',value:'leave_id',sortable: false},
                { text: 'Assigned To',value:'assigned_id',sortable: false},
                { text: 'Day',value :'day',sortable: false},
                { text: 'Period',value :'session',sortable: false},
                { text: 'Class',value :'class',sortable: false},
                { text: 'Location',value :'location',sortable: false},
                { text: 'Leave Type',value :'app_type',sortable: false},
                { text: 'Application Type',value :'type',sortable: false},               
            ],
        }
    },
    async created(){
        await this.fetch_data();
        this.ready = true;
    },
    methods:{
        async fetch_data(){
            await axios.get('subsitude/report/backend/fetch_data.php')
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.userrecord = response.data;
                        }
                    }
                )
        }
    }
}
</script>


